.arrow {
    width: 32px;
    height: 56px;
    margin: 20px;
    background-image: url("./images/arrow.svg");
}

.arrow:last-child{
   transform: rotate(180deg);
}


.iong-arrow {
    width: 51px;
    height: 8px;
    background-image: url("./images/iongArrow.svg");
}

.head-title {
    text-align: center;
    font-size: 3rem;
}

.ellipsis {
    line-height: 150%;
    font-weight: 100;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
  }