.imgText {
    background-color: #b7b7b7;
  
}
.imgText-wrap {
    max-width: 1100px;
    padding-block: 100px;
    margin-inline: auto;
    padding-inline: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 15px;
}

.imgText-card {
    border-radius: 10px;
}

.imgText-text-wrap {
   height: 579px;
   border: dashed 1px blue;
   width: 373px;
}

.img {
    background-size: cover;
    background-position: 50%;
    position: relative;
}

.round-btn {
   position: absolute;
   display: inline-block;
   padding: 10px;
   color: white;
   border-radius: 100px;
   padding-inline: 20px;
   bottom: 20px;
   left: 20px;
}

.text {
    margin-bottom: 250px;
}

.imgText-text{
    font-size: 4rem;
}

@media (max-width:767px) {
    .img {
        background-position: 50% 70%;
    }
    .text {
        margin-bottom: 0px;
    }
    .imgText-text {
        font-size: 2.5rem;
    }
    .imgText-card{
        min-height: 15rem;
    }
     .imgText-wrap {
        grid-template-columns: 1fr;

     }
}