.textSection-wrap {
    max-width: 1100px;
    padding-block: 100px;
    margin-inline: auto;
    padding-inline: 20px;
    height: 500px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 15px;
}

.textSction-card {
}

.textSction-card :hover {
    background-color:#f5f5f5;
    
    transform: background 0.5s;
}

@media ((max-width:767px)) {
    .textSection-card{
       display: flex;
       flex-direction: column;
       gap: 40px;
    }
    
}