.card {
    border: solid 1px gray;
    width: 500px;
    height: 300px;
    background-size: cover;
}

.card-content-wrap {
    height: 250px;
    margin:25px;
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: start;
    gap: 15px;
    
}

.card-title {
    font-size: 2rem;
    color: white;
    text-shadow: 1px 1px 3px black;
}

.card-text {
    color: white;
    text-shadow: 1px 1px 2px black;
}

.card-button {
    font-size: 1.5rem;
    color:white;
    display: inline;
    padding-block: 9px;
    padding-inline: 18px;
    background-color: black;
    cursor: pointer;
    text-decoration: none;
}

.card-button:hover {
    background-color:rgb(59, 59, 59);
}

.card-button:active {
    box-shadow: inset 0 0 10px white;
}

@media (max-Width: 767px) {
    .card {
        width: 100%;
    }
}